define([
    'esri/toolbars/draw',
    'esri/graphic',
    'esri/symbols/CartographicLineSymbol',
    'esri/Color',
    'esri/SnappingManager',
    'esri/layers/FeatureLayer',
    'esri/geometry/webMercatorUtils',
    'esri/symbols/Font',
    'esri/geometry/Point',
    'esri/geometry/Polyline',
    'esri/symbols/TextSymbol',
    'https://unpkg.com/@turf/turf@6/turf.min.js',
], function (
    Draw,
    Graphic,
    CartographicLineSymbol,
    Color,
    SnappingManager,
    FeatureLayer,
    webMercatorUtils,
    Font,
    Point,
    Polyline,
    TextSymbol,
    turf,
    // getLength,
) {
    /*
    * @description: Objeto que contiene los componentes para la herramienta de dibujo
    */
    const ToolDraw = {
        // @params
        toolbarDraw: null, // @params
        map: null, // @params
        controlMeasurementRealTime: null, // @params
        controlMeasurementTable: null, // @params
        lotFeatureLayer: null, // @params
        anotherToolbar: null, // @params
        linearDivision: null, // @params
        callbackAddLineDivision: null, // @params

        // @const
        lineSymbol: null,
        statusDraw: false,
        currentDistance: 0,
        currentCoordinates: [],
        landsFeature: null,
        controllerId: 0,
        measurements: {},

        initToolDraw() {
            this.toolbarDraw = new Draw(this.map);
            this.toolbarDraw.on('draw-end', this.addGraphic.bind(this));
            this.lineSymbol = new CartographicLineSymbol(
                CartographicLineSymbol.STYLE_SHORTDASH,
                new Color([40, 40, 40, 1]),
                3
            )
            this.lineSymbol.setMarker({
                style: 'arrow',
                placement: 'begin-end',
                size: 1
            })

            this.map.on('click', this.clickIntoMap.bind(this));
            this.map.on('mouse-move', this.moveMouseMap.bind(this));
            this.map.on('key-up', (evt) => {
                if (evt.key === 'Escape' && this.statusDraw) {
                    this.currentCoordinates = [];
                    this.currentDistance = 0.00;
                }
            });


            // document.addEventListener('keyup', (evt) => {
            //     if (evt.key === 'Escape' && this.statusDraw) {
            //         this.currentCoordinates = [];
            //         this.currentDistance = 0.00;
            //     }
            // });

            // this.landsFeature = new FeatureLayer(this.lotUrl, {
            //     mode: FeatureLayer.MODE_ONDEMAND,
            //     outFields: ["*"]
            // });

        },

        getUUID() {
            const uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
                const r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
                return v.toString(16);
            });
            return `a${uuid.replace(/-/g, '')}`;
        },

        addGraphic(evt) {
            this.toolbarDraw.deactivate();
            this.controllerId = this.controllerId + 1;
            const idPolylineGraphic = this.getUUID();
            const idLabelGraphic = this.getUUID();
            const graphic = new Graphic(evt.geometry, this.lineSymbol, { id: idPolylineGraphic });
            this.map.graphics.add(graphic);
            this.addLabelToCenterLine(evt.geometry, this.currentDistance, idLabelGraphic);
            this.measurements[idPolylineGraphic] = {
                distance: this.currentDistance.toFixed(2),
                // geometry: evt.geometry,
                // extent: evt.geometry.getExtent(),
                idLabel: idLabelGraphic
            }
            this.addRowToTable(this.measurements[idPolylineGraphic], idPolylineGraphic);
            this.map.enableMapNavigation();
            this.map.setInfoWindowOnClick(true);
            this.statusDraw = false;
            // disable snapping
            // this.map.disableSnapping();
        },

        activateToolDraw(evt) {
            this.anotherToolbar.deactivate();
            const snapManager = this.map.enableSnapping();
            // this.map.enableSnapping();
            snapManager.alwaysSnap = true;

            const layerInfos = [{
                layer: this.lotFeatureLayer
            }];
            snapManager.setLayerInfos(layerInfos);

            this.map.disableMapNavigation();
            this.map.setInfoWindowOnClick(false);
            this.statusDraw = true;
            this.currentCoordinates = [];
            this.toolbarDraw.activate('polyline');
        },

        deactivateToolbarAnotherToolbar() {
            this.toolbarDraw.deactivate();
            this.map.enableMapNavigation();
            this.map.setInfoWindowOnClick(true);
            this.statusDraw = false;
            this.currentDistance = 0.00;
            this.controlMeasurementRealTime.innerHTML = this.currentDistance.toFixed(2);
        },

        addRowToTable(measurementItem, id) {
            // console.log(measurementItem);
            const row = `
                <tr data-idPolyline=${id}>
                    <td class="center-aligned" contenteditable="true" >
                        ${measurementItem.distance}
                    </td>
                    <td class="center-aligned">
                        <i class="fas fa-route"></i>
                    </td>
                    <td class="center-aligned">
                        <span><i class="fas fa-search"></i></span>
                    </td>
                    <td class="center-aligned">
                        <span style="color: #FF5722;"><i class="far fa-trash-alt"></i></span>
                    </td>
                </tr>
            `

            // const table = this.controlMeasurementTable.querySelector('tbody');

            // add function to clic delete row in last column
            this.controlMeasurementTable.insertAdjacentHTML('beforeend', row);
            const deleteRow = this.controlMeasurementTable.querySelector(`tr[data-idPolyline="${id}"] td:last-child`);
            const zoomToGraphic = this.controlMeasurementTable.querySelector(`tr[data-idPolyline="${id}"] td:nth-child(3)`);
            const editDistance = this.controlMeasurementTable.querySelector(`tr[data-idPolyline="${id}"] td:first-child`);
            // add listener when click checkbox
            const convertDivisionLine = this.controlMeasurementTable.querySelector(`tr[data-idPolyline="${id}"] td:nth-child(2)`);

            deleteRow.addEventListener('click', this.deleteRowTable.bind(this));
            zoomToGraphic.addEventListener('click', this.zoomToGraphic.bind(this));
            // add listener when press enter in cell
            editDistance.addEventListener('keydown', (evt) => {
                if (evt.key === 'Enter') {
                    evt.preventDefault();
                    this.editDistance(evt);
                }
            });
            editDistance.addEventListener('input', this.validateNumericInput);
            convertDivisionLine.addEventListener('click', this.parseToLineDivision.bind(this));
        },

        validateNumericInput(evt) {
            const value = evt.target.innerText;
            const regex = /^[0-9]*\.?[0-9]*$/; // Permite números y un punto decimal

            if (!regex.test(value)) {
                evt.target.innerText = value.slice(0, -1); // Elimina el último carácter si no es válido
            }
        },

        deleteRowTable(evt) {
            const graphic = this.getGraphicById(evt.currentTarget.parentElement.dataset.idpolyline);
            this.map.graphics.remove(graphic);
            const idLabel = this.measurements[evt.currentTarget.parentElement.dataset.idpolyline].idLabel;
            const graphicLabel = this.getGraphicById(idLabel);
            this.map.graphics.remove(graphicLabel);
            evt.currentTarget.parentElement.remove();
        },

        zoomToGraphic(evt) {
            const graphic = this.getGraphicById(evt.currentTarget.parentElement.dataset.idpolyline);
            this.map.setExtent(graphic.geometry.getExtent());
        },

        editDistance(evt) {
            const idPolyline = evt.currentTarget.parentElement.dataset.idpolyline;
            const newDistance = parseFloat(evt.currentTarget.textContent);
            const idLabel = this.measurements[idPolyline].idLabel;
            const polyline = this.getGraphicById(idPolyline).geometry;
            const distance = parseFloat(this.measurements[idPolyline].distance);
            const newGeometry = this.updatePolylineLength(newDistance, distance, polyline);
            if (!newGeometry) {
                return;
            }

            this.map.graphics.graphics.forEach(graphic => {
                if (graphic.attributes && graphic.attributes.id === idPolyline) {
                    graphic.setGeometry(newGeometry);
                }
            });

            this.measurements[idPolyline].distance = newDistance.toFixed(2);
            const graphicLabel = this.getGraphicById(idLabel);
            this.map.graphics.remove(graphicLabel);
            this.addLabelToCenterLine(newGeometry, newDistance, idLabel);

        },

        parseToLineDivision(evt) {
            const idPolyline = evt.currentTarget.parentElement.dataset.idpolyline;
            const graphic = this.getGraphicById(idPolyline);
            this.callbackAddLineDivision(graphic.geometry);
            this.deleteRowTable(evt);
            // if (evt.currentTarget.checked) {
            //     const graphic = this.getGraphicById(id);
            //     this.callbackAddLineDivision(graphic.geometry);
            //     // this.linearDivision.add(graphic);
            //     // this.map.addLayer(this.linearDivision);
            // } else {
            //     const graphic = this.getGraphicById(id);

            //     this.linearDivision.remove(graphic);
            // }
        },

        getGraphicById(id) {
            const graphic = this.map.graphics.graphics.filter(graphic => {
                if (graphic.attributes && graphic.attributes.id === id) {
                    return graphic;
                }
            });
            if (graphic.length === 0) {
                return null;
            }
            return graphic[0];
        },

        getDistance(arrayPoints) {
            let totalDistance = 0;
            if (arrayPoints.length < 2) {
                return totalDistance;
            }
            for (let i = 0; i < arrayPoints.length - 1; i++) {
                const distance = esri.geometry.getLength(arrayPoints[i], arrayPoints[i + 1]);
                totalDistance += distance;
            }
            return totalDistance;
        },

        clickIntoMap(evt) {
            if (this.statusDraw) {
                this.currentCoordinates.push(evt.mapPoint);
            }
        },

        moveMouseMap(evt) {
            if (this.statusDraw) {
                if (this.currentCoordinates.length > 0) {
                    this.currentDistance = esri.geometry.getLength(this.currentCoordinates[this.currentCoordinates.length - 1], evt.mapPoint) + this.getDistance(this.currentCoordinates);
                    this.controlMeasurementRealTime.innerHTML = this.currentDistance.toFixed(2);
                }
            }
        },

        addLabelToCenterLine(geometry, distance, idLabelGraphic) {
            const midPoint = this.findMidPoint(geometry, distance);
            const xMidPoint = midPoint.geometry.coordinates[0];
            const yMidPoint = midPoint.geometry.coordinates[1];
            const pointLabel = new Point({
                x: xMidPoint,
                y: yMidPoint,
                spatialReference: { wkid: 4326 }
            })

            const font = new Font("15px", Font.STYLE_NORMAL, Font.VARIANT_NORMAL, Font.WEIGHT_BOLD, "Arial");
            const txtSym = new TextSymbol(distance.toFixed(2), font, new Color([87, 88, 90, 1]));
            txtSym.setOffset(0, 0).setAlign(TextSymbol.DECORATION_OVERLINE);
            // //align vertical center
            txtSym.setVerticalAlignment("middle");
            txtSym.setHaloColor(new Color([255, 255, 255]));
            txtSym.setHaloSize(4);

            // console.log(geometry, midPoint.geometry.coordinates)
            const angle = this.getAngleByLabel(pointLabel, geometry);
            // // rotate text symbol
            txtSym.setAngle(angle);
            const graphicLabel = new Graphic(pointLabel, txtSym, { id: idLabelGraphic });
            this.map.graphics.add(graphicLabel);
            // return idLabelGraphic;
        },

        findMidPoint(polyline, distance) {
            const polylineGeomUtm = webMercatorUtils.webMercatorToGeographic(polyline)
            const line = turf.lineString(polylineGeomUtm.paths[0]);
            const options = { units: 'meters' };
            const along = turf.along(line, distance / 2, options);
            return along
        },

        getAngleByLabel(centerPoint, polyline) {
            const polylineGeomUtm = webMercatorUtils.webMercatorToGeographic(polyline)
            const startPoint = polylineGeomUtm.paths[0][0];
            const startPointTurf = turf.point([startPoint[0], startPoint[1]]);
            const centerPointTurf = turf.point([centerPoint.x, centerPoint.y]);
            const slice = turf.lineSlice(startPointTurf, centerPointTurf, turf.lineString(polylineGeomUtm.paths[0]));
            const points = slice.geometry.coordinates.slice(-2);
            let angle = turf.bearing(turf.point(points[0]), turf.point(points[1]));
            angle = angle < 0 ? 180 + angle : angle;
            return angle - 90;
        },

        updatePolylineLength(newDistamce, distance, polyline) {
            const polylineGeomUtm = webMercatorUtils.webMercatorToGeographic(polyline)
            let vertices = polylineGeomUtm.paths[0];
            if (newDistamce === 0) {
                return false;
            }
            else if (newDistamce < distance) {
                const line = turf.lineString(vertices);
                const options = { units: 'meters' };
                const sliced = turf.lineSliceAlong(line, 0, newDistamce, options);
                vertices = sliced.geometry.coordinates;
            }
            else if (newDistamce > distance) {
                const lastTwoCoords = vertices.slice(-2);
                const line = turf.lineString(lastTwoCoords);
                const bearing = turf.bearing(turf.point(line.geometry.coordinates[0]), turf.point(line.geometry.coordinates[1]));
                const options = { units: 'meters' };
                const addDistance = newDistamce - distance;
                const point = turf.point(line.geometry.coordinates[1]);
                const destintation = turf.destination(point, addDistance, bearing, options);
                vertices[vertices.length - 1] = destintation.geometry.coordinates;
            }

            // console.log(vertices);
            // map.graphics.clear();

            polyline = new Polyline({
                paths: [vertices],
                spatialReference: polylineGeomUtm.spatialReference
            });

            const response = webMercatorUtils.geographicToWebMercator(polyline);
            return response;
            // map.graphics.add(new Graphic(a, lineSymbol));
            // distance = newDistamce;
            // addLabelToCenter(a, distance);
            // window.polyline = a;
            // return vertices;
        },

        removeAllGraphicsIntoMeasurements() {
            this.controlMeasurementTable.innerHTML = '';
            // iterame this.measurements and delete all graphics by id
            for (const key in this.measurements) {
                const graphic = this.getGraphicById(key);
                this.map.graphics.remove(graphic);
                const idLabel = this.measurements[key].idLabel;
                const graphicLabel = this.getGraphicById(idLabel);
                this.map.graphics.remove(graphicLabel);
            }
            this.measurements = {};

            this.controlMeasurementRealTime.innerHTML = '0.00';
        },
        // },
    };
    return ToolDraw;
});